import React, { useState, useContext, useEffect } from 'react'
import LoadingContent from '../../../theme/components/loading-content'
import BoardPage from '../board-page'
import { list } from '../../services/board-service'
import { useProjectId } from '../../../projects/hooks'
import PageContext from '../../../theme/components/page/page-context'

export default function BacklogPage () {
  const [board, setBoard] = useState(null)
  const [error, setError] = useState(null)
  const projectId = useProjectId()
  const { setPageTitle } = useContext(PageContext)

  setPageTitle('Backlog')

  useEffect(() => {
    async function boot () {
      const boards = await list(projectId, { type: 'backlog' })

      if (boards.length > 0) {
        setBoard(boards[0])
      } else {
        setError('No backlog board found')
      }
    }

    boot()
  }, [projectId])

  if (error) {
    return <div className="alert alert-danger">{error}</div>
  }

  if (!board) {
    return <LoadingContent />
  }

  return <BoardPage board={board} />
}
